@import '../../App';

.cell {
  width: clamp(30px, 4vw, 60px);
  height: clamp(30px, 4vw, 60px);
  border: 1px solid $global-black;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: 800;
  font-size: 30px;
  cursor: pointer;
  background-color: $global-second-green;

  &_revealed {
    font-size: clamp(20px, 3vw, 40px);
    background-color: $global-green;
  }
}
