@import '../../App';

.board {
  display: flex;
  flex-direction: column;
  align-items: center;

  &__header {
    display: flex;
    flex-direction: column;
    gap: 5px;
    margin-bottom: 10px;
  }

  &__name {
    font-size: clamp(15px, 3vw, 25px);
    margin: 0 auto;
    color: $global-orange;
  }

  &__menu {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: clamp(200px, 30vw, 400px);
  }

  &__flags {
    font-size: clamp(20px, 3vw, 40px);
    display: flex;
    align-items: center;
  }

  &__button {
    width: clamp(50px, 6vw, 70px);
    height: clamp(50px, 6vw, 70px);
    display: flex;
    justify-content: center;
    align-items: center;
    background-image: url("../../images/emogi_smile.svg");
    background-size: clamp(100px, 15vw, 120px) clamp(100px, 15vw, 120px);
    background-repeat: no-repeat;
    background-position: center;

    &_win {
      background-image: url("../../images/emogi_happy.svg");
    }

    &_lose {
      background-image: url("../../images/emogi_sad.svg");
    }
  }

  &__cells {
    display: flex;
  }
}